* {
  margin: 0px;
  padding: 0px;
}

body {
  background-color: purple;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  padding: 3em;
}

header img {
  width: 15%;
}

main {
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
}

main img {
  width: 45%;
}

.content {
  width: 40%;
  padding-top: 2em;
}

.content p {
  padding-top: 1em;
}
.content button {
  width: 120px;
  padding: 1.2em;
  outline: none;
  border-radius: 30px;
  color: purple;
  cursor: pointer;
  border: none;
  margin-top: 2em;
  font-weight: bold;
}

footer i {
  margin: 1em;
}

footer {
  text-align: end;
}

footer span {
  background-color: white;
  border-radius: 10px;
  padding: 0.5em;
}

.fa-facebook-f,
.fa-twitter {
  color: blue;
}

.fa-instagram {
  color: brown;
}

@media (max-width: 780px) {
  main {
    flex-direction: column;
    justify-content: center;
  }

  main img {
    margin: 0 auto;
  }

  .content {
    width: 100%;
  }

  footer {
    margin-top: 2em;
    text-align: center;
  }
}
